/* home */
.home {
  padding: 1rem 7rem 0rem 7rem;
}
.home ol {
  padding: 0rem 0rem 0rem 2.5rem;
}
.home p {
  padding: 0.5rem;
  text-align: justify;
  line-height: 1.3rem;
}
.home h3 {
  color: rgb(255, 200, 21);
  font-weight: 400;
  padding-left: 0.5rem;
}
.home li {
  border-bottom: 0.5px solid white;
  padding: 0.2rem;
  margin: 0.5rem;
}

.home ol li h4{
  font-weight: 400;
}

.home a {
  color: rgb(255, 200, 21);
  float: right;
}
.home ol li a:hover {
  background-color: rgba(75, 75, 75, 0);
  color: white;
}


@media only screen and (max-width: 1024px) {
  .home {
    padding: 1rem 3rem 0rem 3rem;
  }
  
}


/* For mobile phones: */

@media only screen and (max-width: 767px) {
  .home {
    padding: 1rem 1rem 0rem 1rem;
  }
  
}


